*{box-sizing:border-box;margin:0;padding:0}html{scroll-behavior:smooth}body{margin:0 0 3rem;font-family:"Montserrat",sans-serif;font-size:.938rem;background-color:#161927;color:#babcc4}h1,h2,h3,h4{color:#f1f2f3;font-weight:600}ul{list-style:none}a{text-decoration:none}img{max-width:100%;height:auto}::-webkit-scrollbar{width:.60rem;height:0.25rem;background-color:#6c7189;border-radius:0.5rem}::-webkit-scrollbar-thumb{background-color:#515467;border-radius:0.5rem}::-webkit-scrollbar-thumb:hover{background-color:#9fa1ad}@media screen and (min-width: 568px){::-webkit-scrollbar{height:0.5rem}}.section{padding:2rem 0 4rem}.section__title{font-size:clamp(24px, 5vw, 1.5rem)}.section__subtitle{display:block;font-size:.813rem;margin-bottom:3rem}.section__title,.section__subtitle{text-align:center}@media screen and (min-width: 768px){.section{padding:5rem 0 4rem}}@media screen and (min-width: 1024px){.section{height:100vh}}.container{margin-left:1.5rem;margin-right:1.5rem}.grid{display:grid;grid-gap:1.5rem;gap:1.5rem}@media screen and (max-width: 350px){.container{margin-left:1rem;margin-right:1rem}}.button{display:inline-block;background-color:#576ee0;color:white;padding:1rem;border-radius:.5rem;font-weight:500}.button:hover{background-color:#435acb}.button__icon{font-size:1.25rem;margin-left:.5rem;transition:.3s}.button--flex{display:inline-flex;align-items:center}

.header{width:100%;color:red;position:fixed;left:0;bottom:0;z-index:100;background-color:#161927}.nav{height:3rem;display:flex;justify-content:space-between;align-items:center}.nav__logo{margin-left:1.5rem;color:#576ee0;font-weight:500;height:35px;width:35px;border-radius:50%;border:2px solid #576ee0;display:flex;justify-content:center;align-items:center}.nav__logo span{font-size:1.25rem;font-weight:600;margin:auto}.nav__logo:hover{color:#576ee0}.nav__toggle{color:#f1f2f3;font-weight:500;font-size:1.1rem;cursor:pointer}.nav__toggle:hover{color:#576ee0}.nav__list{grid-template-columns:repeat(3, 1fr);grid-gap:2rem;gap:2rem}.nav__link{display:flex;flex-direction:column;align-items:center;font-size:.813rem;color:#f1f2f3;font-weight:500}.nav__link:hover{color:#576ee0}.nav__icon{font-size:1.2rem}.nav__close{position:absolute;right:1.3rem;bottom:.5rem;font-size:1.5rem;cursor:pointer;color:#576ee0}.nav__close:hover{color:#435acb}.nav__btns{display:flex;align-items:center}.change-theme{font-size:1.125rem;color:#f1f2f3;margin-right:1rem;cursor:pointer}.change-theme:hover{color:#576ee0}.show-menu{bottom:0 !important}@media screen and (max-width: 767px){.nav__menu{position:fixed;bottom:-100%;left:0;width:100%;background-color:#161927;padding:2rem 1.5rem 4rem;box-shadow:0 -1px 4px rgba(0,0,0,0.15);border-radius:1.5rem 1.5rem 0 0;transition:0.3s}}@media screen and (min-width: 768px){.header{top:0;bottom:auto;bottom:initial;padding:0 1rem}.nav{height:4.5rem;grid-column-gap:1rem;-webkit-column-gap:1rem;column-gap:1rem}.nav__icon,.nav__close,.nav__toggle{display:none}.nav__list{display:flex !important;grid-column-gap:2rem !important;-webkit-column-gap:2rem !important;column-gap:2rem !important}.nav__menu{margin-left:auto}.change-theme{margin:0}}@media screen and (max-width: 350px){.nav__menu{padding:2rem .25rem 4rem}.nav__list{grid-column-gap:0;-webkit-column-gap:0;column-gap:0}}

.layout{display:flex;height:100vw}.layout__social,.layout__email{display:none}.layout__content__page{padding:0 1rem}@media screen and (min-width: 568px){.layout__content__page{padding:0 50px}}@media screen and (min-width: 768px){.layout__social{display:block;width:40px;position:fixed;bottom:0px;left:40px;right:auto;z-index:10}.layout__social-icon{font-size:1.25rem;color:#576ee0}.layout__social-icon:hover{color:#435acb}.layout__social ul{display:flex;flex-direction:column;align-items:center;margin:0px;padding:0px;list-style:none}.layout__social ul::after{content:"";display:block;width:1px;height:90px;margin:0px auto;background-color:#576ee0}.layout__social li{padding:1rem}.layout__social li:last-child{margin-bottom:1.5rem}.layout__social li:hover{-webkit-transform:translateY(-3px);transform:translateY(-3px)}.layout__email{display:block;width:40px;position:fixed;bottom:0px;left:auto;right:40px;z-index:10}.layout__content__page{padding:0 100px}}@media screen and (min-width: 768px){.layout__content__page{padding:0 150px}}

.home{min-height:100vh;display:grid;align-items:center}.home__container{grid-gap:1rem;gap:1rem}.home__content{grid-template-columns:.5fr 3fr;padding-top:3.5rem;align-items:center}.home__blob{width:200px;fill:#576ee0}.home__blob-img{width:170px}.home__data{grid-column:1/3}.home h1{transition-delay:100ms;color:#576ee0}.home__title{font-size:clamp(40px, 8vw, 80px);transition-delay:200ms}.home__subtitle{font-size:clamp(30px, 6vw, 60px);color:#9fa1ad;font-weight:500;margin-bottom:.75rem;transition-delay:300ms}.home__description{margin-bottom:2rem;transition-delay:400ms}.home__scroll-button{color:#576ee0;transition:.3s}.home__scroll-button:hover{-webkit-transform:translateY(0.25rem);transform:translateY(0.25rem)}.home__scroll-mouse{font-size:2rem}.home__scroll-name{font-size:.813rem;color:#f1f2f3;font-weight:500;margin-right:.25rem}.home__scroll-arrow{font-size:1.25rem}@media screen and (max-width: 350px){.home__content{grid-template-columns:.25fr 3fr}.home__blob{width:180px}}@media screen and (min-width: 568px){.home__container{grid-row-gap:2rem !important;row-gap:2rem !important}.home__content{grid-template-columns:-webkit-max-content 1fr 1fr;grid-template-columns:max-content 1fr 1fr}.home__data{grid-column:initial}.home__img{order:1;justify-self:center}}@media screen and (min-width: 768px){.home__container{grid-row-gap:5rem !important;row-gap:5rem !important}.home__content{padding-top:5.5rem;grid-column-gap:2rem;-webkit-column-gap:2rem;column-gap:2rem}.home__blob{width:270px}.home__scroll{display:block}}@media screen and (min-width: 1024px){.home{margin-left:2rem}.home__blob{width:320px}.home__social{-webkit-transform:translateX(-6rem);transform:translateX(-6rem)}}

.contact{justify-content:center;align-items:center}.contact__title{font-weight:600;color:#f1f2f3;font-size:clamp(40px, 5vw, 60px);text-align:center;margin-bottom:1.5rem}.contact__purpose{text-align:center;font-size:1.15rem;color:#babcc4}.contact__email{margin-top:1rem;font-size:1.25rem;color:#576ee0;font-weight:600}.contact__button{display:flex;justify-content:center;margin-top:1.5rem}@media screen and (min-width: 768px){.contact__container{max-width:605px}.contact__button{margin-top:3rem}}

.about{justify-content:center;align-items:center}.about__img{width:200px;border-radius:0.5rem;justify-self:center;align-self:center}.about__blob{width:220px;fill:#576ee0}.about__blob-img{width:215px}.about__description{margin-bottom:2.5rem}.about__info{display:flex;justify-content:space-evenly;margin-bottom:2.5rem}.about__info-name{font-size:.813rem}.about__info-title{font-size:1.25rem;font-weight:600;color:#f1f2f3}.about__info-title,.about__info-name{display:block;text-align:center}.about__buttons{display:flex;justify-content:center}@media screen and (min-width: 768px){.about__container{width:605px;margin:auto}}@media screen and (min-width: 1024px){.about__container{width:800px;margin:auto}}

.skills__container{grid-row-gap:0;row-gap:0;justify-content:center;align-items:center}.skills__content{margin-bottom:2.5rem}.skills__header{display:flex;align-items:center;cursor:pointer;margin-bottom:1rem}.skills__icon,.skills__arrow{font-size:2rem;color:#576ee0}.skills__icon{margin-right:.75rem}.skills__title{font-size:1.125rem}.skills__subtitle{font-size:.813rem;color:#9fa1ad}.skills__list{display:flex;flex-wrap:wrap}.skills__data{margin-right:1rem;padding:0.75rem;border-radius:0.5rem;background-color:#112240;margin-bottom:.5rem}.skills__arrow{margin-left:auto}.skills__name{font-size:.938rem;font-weight:500}.skills__close .skills__list{height:0;overflow:hidden}.skills__open .skills__list{height:-webkit-max-content;height:max-content;margin-bottom:2.5rem}.skills__open .skills__arrow{-webkit-transform:rotate(-180deg);transform:rotate(-180deg)}

.experience__container{grid-auto-flow:column;grid-template:-webkit-max-content auto;grid-template:max-content auto}.experience__tabList{margin:0;padding:0}.experience__button{transition:all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);display:flex;align-items:center;width:100%;height:42px;padding:0px 20px 2px;border:0;border-left:3px solid #b6c1fc;background-color:transparent;color:#f1f2f3;font-size:.938rem;text-align:left;white-space:nowrap;border-radius:0px;cursor:pointer}.experience__button-active{color:#576ee0;border-left:3px solid #576ee0;background-color:#112240}.experience__button:hover{color:#576ee0;background-color:#112240}.experience__tabPanels{margin-left:1.25rem;padding:10px 5px}.experience__company{font-size:1.25rem}.experience__company-link{display:inline-block;transition:all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);color:#576ee0;text-decoration:none;-webkit-text-decoration-skip:ink;text-decoration-skip-ink:auto;position:relative}.experience__company-link::after{content:"";display:block;width:0px;height:1px;position:relative;top:1px;bottom:0.37em;background-color:#576ee0;transition:all 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);opacity:0.5}.experience__company-link:hover{color:#576ee0;outline:0px}.experience__company-link:hover::after{width:100%}.experience__duration{font-size:.75rem;color:#9fa1ad;margin-bottom:1.125rem}.experience__reponsibilities{list-style:none;margin:0px;padding:0px;font-size:1.125rem}.experience__reponsibilities li{position:relative;padding-left:1.25rem;margin-bottom:10px}.experience__reponsibilities li::before{content:"▹";position:absolute;left:0px;color:#576ee0}@media screen and (max-width: 568px){.experience__container{grid-auto-flow:row;grid-template:-webkit-max-content auto;grid-template:max-content auto}.experience__tabList{display:flex;overflow-x:auto}.experience__button{border-left:0}.experience__button-active{color:#576ee0;border-bottom:4px solid #576ee0;background-color:#112240}}@media screen and (min-width: 768px){.experience__container{width:605px;margin:auto}}@media screen and (min-width: 1024px){.experience__container{width:750px;margin:auto}}

.footer{display:flex;justify-content:center;align-items:center;flex-direction:column;height:auto;min-height:70px;margin-bottom:3.5rem}.footer__social-icon{font-size:1.25rem;color:#576ee0}.footer__social-icon:hover{color:#435acb}.footer__social ul{display:flex;align-items:center;justify-content:center;list-style:none;margin:0px;padding:0px}.footer__social li{padding:1rem}.footer__social li:hover{-webkit-transform:translateY(-3px);transform:translateY(-3px)}.footer__location{text-align:center;padding:1rem}.footer__location div{margin-bottom:.5rem}@media screen and (min-width: 768px){.footer__social ul{display:none}}

.work{height:auto}.work__list{display:flex;flex-wrap:wrap;flex-direction:column;align-items:center;justify-content:center}.work__card{background-color:#112240;border-radius:.5rem;margin-bottom:2rem;padding:1rem;opacity:0.7}.work__card .work__link{display:none}.work__card:hover{transition:opacity 0.75s;-webkit-transition:opacity 0.75s;opacity:1}.work__card:hover .work__link{position:absolute;bottom:10%;right:2%;cursor:pointer;font-size:2rem;height:50px;width:50px;border-radius:50%;background-color:#576ee0;display:flex;justify-content:center;align-items:center;-webkit-animation:fadeInAnimation ease 0.75s;animation:fadeInAnimation ease 0.75s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards}.work__card:hover .work__link:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}.work__card:hover .work__link i{padding:0.5rem;color:#f1f2f3}@-webkit-keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}@keyframes fadeInAnimation{0%{opacity:0}100%{opacity:1}}.work__image{position:relative;padding-bottom:.75rem;border-radius:3px}.work__image img{height:100%;width:100%;object-fit:cover}.work__name{color:#f1f2f3;font-weight:600;font-size:1.125rem;cursor:pointer}.work__name a{color:inherit}.work__skills{display:flex;flex-wrap:wrap;margin-bottom:0.75rem;margin-top:0.75rem}.work__skills-badge{border:1px solid #576ee0;font-size:.75rem;color:#576ee0;font-weight:600;margin-right:.75rem;border-radius:3px;padding:2px 4px;margin-bottom:0.75rem}.work__description{color:#9fa1ad;margin-bottom:1rem}@media screen and (min-width: 768px){.work__list{flex-direction:row;justify-content:space-between}.work__card{flex:0 0 47%;min-height:25vw;margin-bottom:1.5rem}.work__image{height:calc(25vw - .75rem)}}@media screen and (min-width: 1024px){__overflow{height:120px;overflow-y:auto}}

